//  css

interface IAddBoard {
  boardId: string;
}

function App() {
  const [toDos, setToDos] = useRecoilState(toDoState);
  const onDragEnd = (info: DropResult) => {
    const { draggableId, destination, source } = info;

    /* console.log(info); */

    if (!destination) return;
    ///destination  =      

    if (destination?.droppableId === source.droppableId) {
      //

      setToDos((allBoards) => {
        const boardCopy = [...allBoards[source.droppableId]];
        //source.droppableId  array 
        //droppableId ==> array  (To Do,doing)
        const taskObj = boardCopy[source.index];
        //boardCopy[source.index]   
        //source.index  index 
        //boardCopy splice   taskObj 

        boardCopy.splice(source.index, 1);
        //source.index  index 
        boardCopy.splice(destination?.index, 0, taskObj);
        //destination.index   index 
        /* draggableId toDo  string (=> todo:["a","b"]) 
        (destination?.index, 0, draggableId)  
         string --> ToDoList  array  board() 
          taskObj*/

        return {
          ...allBoards,
          [source.droppableId]: boardCopy,
        };
        // (boardCopy) +     (allBoards)
        /* [source.droppableId]: boardCopy 
        []: es6  key (,)  
        "To Do": boardCopy js 
         splice  boardCopy source.droppableId */
      });
    } //    

    if (destination.droppableId !== source.droppableId) {
      //

      setToDos((allBoards) => {
        const sourceBoard = [...allBoards[source.droppableId]];
        //()
        const taskObj = sourceBoard[source.index];
        //sourceBoard[source.index]   
        //source.index  index 
        const destinationBoard = [...allBoards[destination.droppableId]];
        //()
        sourceBoard.splice(source.index, 1);
        destinationBoard.splice(destination?.index, 0, taskObj);

        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard,
        };
      });
    } //     
  };

  const { register, setValue, handleSubmit } = useForm<IAddBoard>();
  //  
  const [addingBoard, setAddToggle] = useState(false);
  /*   input     if   
      useState  */
  const addBoard = ({ boardId }: IAddBoard) => {
    setToDos((allBoards) => {
      const newAddBoard: IToDoState = { [boardId]: [] };

      return {
        ...allBoards,
        ...newAddBoard,
      };
    });
    setAddToggle(false);
    setValue("boardId", "");
  };

  const onClickAddBoard = () => {
    setAddToggle(true);
    openModal();
  };
  //  

  const [modalIsOpen, setIsOpen] = useState(false);
  //
  function openModal() {
    setIsOpen(true);
  }
  function closeModal() {
    setIsOpen(false);
  }

  return (
    <FullScreen>
      {addingBoard && (
        <Modal
          isOpen={modalIsOpen}
          ariaHideApp={false}
          onRequestClose={closeModal}
          style={modalCustomStyles}
        >
          <AddBoardTitle>
            <h1> </h1>
            <AddBoardCancelBtn onClick={closeModal}>x</AddBoardCancelBtn>
          </AddBoardTitle>
          <AddBoardForm onSubmit={handleSubmit(addBoard)}>
            <input
              {...register("boardId", { required: true })}
              type="text"
              placeholder=" ."
            />
          </AddBoardForm>
        </Modal>
      )}

      <Navigation>
        <Title>Memo Board</Title>
        <ButtonDiv>
          <BoardAddButton onClick={onClickAddBoard}>
            <i className="fa-solid fa-plus"></i>
          </BoardAddButton>
        </ButtonDiv>
      </Navigation>

      <DragDropContext onDragEnd={onDragEnd}>
        <Wrapper>
          {Object.keys(toDos).map((boardId) => (
            <Board boardId={boardId} key={boardId} toDos={toDos[boardId]} />
          ))}
        </Wrapper>
      </DragDropContext>
    </FullScreen>
  );
}

export default App;

