import React, { useRef, useState } from "react";
import { Draggable } from "react-beautiful-dnd";
import styled from "styled-components";
import { useSetRecoilState } from "recoil";
import { toDoState } from "../atoms";
import { useForm } from "react-hook-form";

const ButtonDiv = styled.div`
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  right: 0.4rem;
  gap: 2px;
  height: 100%;
`;

const Button = styled.button`
  height: 2rem;
  width: 2rem;
  border-color: transparent;
  border-radius: 0.5rem;
  background-color: transparent;
  &:hover {
    background-color: ${(props) => props.theme.buttonColor};
    cursor: pointer;
  }
  i {
    color: #706e6e;
  }
`;

const CardSpanDiv = styled.div``;

interface CardProps {
  isDragging: boolean;
  editing?: boolean;
}

const Card = styled.div<CardProps>`
  background-color: ${(props) =>
    props.isDragging ? "#74b9ff" : props.theme.cardColor};
  border-radius: 5px;
  margin-bottom: 5px;
  padding: 15px 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  &:not(:hover):not(:focus-within) ${ButtonDiv} {
    opacity: 0;
  }
  & > :first-child {
    width: 100%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    transition: width 0.3s;
    margin-top: 0.1rem;
  }
  &:hover > ${CardSpanDiv}, &:focus-within > ${CardSpanDiv} {
    width: ${(props) => (props.editing ? null : "8.75rem")};
  }
`;

interface IDraggableCardProps {
  index: number;
  todoId: number;
  todoText: string;
  boardId: string;
}

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  gap: 5px;
`;

interface IForm {
  toDo: string;
}

const Form = styled.form`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: none;
`;

const Input = styled.input`
  width: 100%;
  height: auto;
  margin: none;
  border: none;
  padding: none;
  background: transparent;
  outline: transparent;
`;

const CancelButton = styled.button`
  display: flex;
  justify-content: center;
  align-items: center;
  height: 1.7rem;
  width: 1.7rem;
  border-color: transparent;
  border-radius: 0.5rem;
  background-color: transparent;
  &:hover {
    background-color: ${(props) => props.theme.buttonColor};
    cursor: pointer;
  }
  i {
    color: #706e6e;
  }
`;

function DraggableCard({
  todoText,
  todoId,
  index,
  boardId,
}: IDraggableCardProps) {
  const setToDos = useSetRecoilState(toDoState);
  const [editing, setEdit] = useState(false);
  const { register, handleSubmit, setValue } = useForm<IForm>();

  const onValid = ({ toDo }: IForm) => {
    const editToDo = {
      id: todoId,
      text: toDo,
    };

    setToDos((allBoards) => {
      const boardCopy = [...allBoards[boardId]];
      boardCopy[index] = editToDo;

      return {
        ...allBoards,
        [boardId]: boardCopy,
      };
    });
    setValue("toDo", "");
    setEdit((prev) => false);
  };

  const cardEdit = () => {
    setEdit((prev) => true);
  };
  // 

  const cardEditCancel = (event: any) => {
    setEdit((prev) => false);
    setValue("toDo", "");

    if (event.key === `Enter`) {
      event.preventDefault();
    }
  };
  // 

  const cardDelete = () => {
    setToDos((allBoards) => {
      return {
        ...allBoards,
        [boardId]: [...allBoards[boardId].filter((toDo) => toDo.id !== todoId)],
        // todo.id     
      };
    });
  };
  // 

  console.log(boardId, todoId, todoText, index);

  return (
    <Draggable draggableId={todoId + ""} index={index}>
      {/* +string => number , number+""=>string */}
      {(provided, snapshot) => (
        <Card
          isDragging={snapshot.isDragging} //  
          ref={provided.innerRef}
          {...provided.draggableProps}
          {...provided.dragHandleProps}
        >
          {editing ? (
            <Wrapper>
              <Form onSubmit={handleSubmit(onValid)}>
                <Input
                  {...register("toDo", { required: true })}
                  type="text"
                  placeholder="   ."
                />
              </Form>
              <CancelButton onClick={cardEditCancel}>
                <i className="fa-solid fa-circle-xmark"></i>
              </CancelButton>
            </Wrapper>
          ) : (
            <>
              <CardSpanDiv>{todoText}</CardSpanDiv>
              <ButtonDiv>
                <Button onClick={cardEdit}>
                  <i className="fa-solid fa-pen"></i>
                </Button>
                <Button onClick={cardDelete}>
                  <i className="fa-solid fa-trash"></i>
                </Button>
              </ButtonDiv>
            </>
          )}
        </Card>
      )}
    </Draggable>
  );
}

export default React.memo(DraggableCard);

